.App {
  text-align: center;
  scroll-behavior: smooth;
}

.container{
  width: 100%;
}

.title{
  font-size: 3rem;
  font-weight: bold;
  color: #242e42;
}

section{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.App-header {
  background-color: #242e42;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header .head{
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 8rem;
}

.App-header .head h2{
  font-size: 2.5rem;
  color: #7d91ba;
}

.App-header .head h4{
  font-size: 1.7rem;
  color: #f24283;
}

.App-header .head img{
  margin: .5rem;
  border-radius: 50%;
  border: 1rem solid #f24283;
  width: 17rem;
}

nav{
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: #7d91ba;
  height: 5rem;
  width: 100%;
}

nav .logo{
  height: 12vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .logo{
    animation: App-logo-spin infinite 20s linear;
  }
}


nav ul{
  display: flex;
  align-items: center;
  justify-content: center;
}

nav ul li{
  list-style: none;
}

nav ul li a{
  margin: 0 2rem;
  text-decoration: none;
  color: #fff;
}

nav ul li a:hover{
  color: #f24283;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.skills ul{
  display: grid;
  grid-template-columns: repeat(9, auto);
  gap: .5rem;
}

.skills ul li{
  display: flex;
  flex-direction: column;
  text-decoration: none;
  align-items: center;
  justify-content: center;
  color: #ddd;
}

.skills ul li i{
  font-size: 2rem;
}

.Summary{
  background: #7d91ba;
}


.Summary .About .text-content{
  padding: 3rem;
}
.Summary .About{
  padding: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Summary .About img{
  width: 17rem;
  border-radius: 10rem;
  border: 1rem solid #242e42;
}

.Summary .About .text-content p{
  font-size: 1.3rem;
  color: #eee;
  text-align: left;
}

.Protfolio{
  background: #242e42;
}

.Protfolio .repos-list{
  display: flex;
}

.Protfolio .title{
  color: #ddd;
}

.Contact{
  background: #7d91ba;
}

.Contact .info-list{
  display: grid;
  grid-template-columns: repeat(4, auto);
}
.Contact .info-list a{
  text-decoration: none;
  color: #242e42;
  font-size: 1.2rem;
  padding: 1rem 2rem;
  margin: 2rem .4rem;
  border: .15rem solid #242e42;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: .5rem;
}

.Contact .info-list a:hover{
  border: .15rem solid #ddd;
  color: #ddd;
  cursor: pointer;
}

.Contact a i{
  font-size: 3rem;
}


.footer{
  color: #fff;
  background: #242e42;
  font-size: 1.2rem;
  font-weight: lighter;
  letter-spacing: .15rem;
  height: 4rem;
  text-align: center;
  padding-top: 2rem;
}



@media (max-width: 771px){
  .title{
    font-size: 2rem;
  }
  
  .App-header {
    min-height: 60vh;
    font-size: calc(8px + 2vmin);
  }
  
  .App-header .head{
    padding-top: 4rem;
  }
  
  .App-header .head img{
    margin: .2rem;
    width: 10rem;
  }
  
  nav{
    height: 3rem;
  }
  
  nav ul li a{
    margin: 0 1rem;
  }
  
  .skills ul{
    grid-template-columns: repeat(5, auto);
  }
  
  .skills ul li i{
    font-size: 1.5rem;
  }
  
  .Summary .About .text-content{
    padding: 2rem;
  }
  .Summary .About{
    padding: 2.5rem;
    flex-direction: column-reverse;
  }
  
  .Summary .About img{
    border-radius: 7rem;
    width: 13rem;
    border: 2rem solid #242e42;
  }
  
  .Summary .About .text-content p{
    font-size: 1rem;
  }
  
  .Protfolio{
    background: #242e42;
  }
  
  .Protfolio .repos-list{
    display: grid;
    grid-template-columns: repeat(2, auto);
    gap: .5rem;
  }
  
  .Contact .info-list{
    display: grid;
    grid-template-columns: repeat(2, auto);
  }
  .Contact .info-list a{
    font-size: 1rem;
    padding: .5rem 1rem;
    margin: .6rem;
  }
  
  .Contact .info-list a:hover{
    border: .15rem solid #ddd;
  }
  
  .Contact a i{
    font-size: 1.6rem;
  }
  
}


@media (max-width: 461px){

  .App-header {
    min-height: 40vh;
  }

  .title{
    font-size: 1.6rem;
  }
  
  .App-header .head{
    padding-top: .6rem;
    height: 55vh;
  }

  .App-header .head h2{
    font-size: 1rem;
  }
  
  .App-header .head h4{
    font-size: .7rem;
  }
  
  
  .App-header .head img{
    width: 7.5rem;
    border: .5rem solid #f24283;
  }
  
  nav ul li a{
    margin: 0 1rem;
  }
  
  .skills ul{
    grid-template-columns: repeat(4, auto);
  }

  .Summary .About .text-content{
    padding: 1rem;
  }

  .Summary .About{
    padding: .35rem;
  }
  
  .Summary .About img{
    width: 9rem;
    border: .5rem solid #242e42;
  }

  .Protfolio .repos-list{
    grid-template-columns: repeat(1, auto);
  }

  .Contact .info-list a{
    padding: .2rem;
  }

  .footer{
    font-size: .9rem;
    letter-spacing: 0.07rem;
    height: 2rem;
    padding-top: 1rem;
  }
}