.RepoCard{
    border: .1rem solid #f24283;
    border-radius: 2.5rem;
    background: #fff;
    width: 20rem;
    margin: .2rem;
    .image-content{
        background: #f24283;
        width: 100%;
        height: 12rem;
        border-radius: 2rem 2rem 2rem 0rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .text-content{
        padding: .5rem;
        
    }
    .avatar{
        width: 11rem;
        border-radius: 50%;
    }
    h1{
        color: #999;
    }
    p{
        color: #555;
        font-size: .9rem;
        padding: .5rem;
    }
}

.MainBTN{
    background: #f24283;
    padding: .6rem 1rem;
    margin: 1rem 0;
    border-radius: 2rem;
    color: #fff;
    font-size: 1.2rem;
    border: none;
    &:hover{
        background: #666;
    }
    a{
        text-decoration: none;
        color: #fff;
    }
}


.Pagination{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 1rem;
    button{
        width: 2rem;
        height: 2rem;
        border-radius: 50%;
        cursor: pointer;
        transition: all 0.3s ease;
        background: #999;
        color: #999;
        border: none;
        margin: .7rem;
        padding: .5rem;
        &.active{
            font-weight: 900;
            background: #f24283;
            color: #f24283;
        }
    }
}



@media (max-width: 661px){
    .RepoCard{
        width: 16rem;
        .image-content{
            height: 10rem;
        }
        .text-content{
            padding: .1rem;
            
        }
        .avatar{
            width: 8rem;
        }
        
        p{
            padding: .25rem;
        }
    }
    
    .MainBTN{
        background: #f24283;
        padding: .6rem 1rem;
        margin: 1rem 0;
        border-radius: 2rem;
        color: #fff;
        font-size: 1.2rem;
        border: none;
        &:hover{
            background: #666;
        }
        a{
            text-decoration: none;
            color: #fff;
        }
    }
    
    
    .Pagination{
        margin-top: .5rem;
        button{
            width: 1rem;
            height: 1rem;
            margin: .3rem;
            padding: .15rem;
        }
    }
}


