.RepoCard {
  border: 0.1rem solid #f24283;
  border-radius: 2.5rem;
  background: #fff;
  width: 20rem;
  margin: 0.2rem;
}
.RepoCard .image-content {
  background: #f24283;
  width: 100%;
  height: 12rem;
  border-radius: 2rem 2rem 2rem 0rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.RepoCard .text-content {
  padding: 0.5rem;
}
.RepoCard .avatar {
  width: 11rem;
  border-radius: 50%;
}
.RepoCard h1 {
  color: #999;
}
.RepoCard p {
  color: #555;
  font-size: 0.9rem;
  padding: 0.5rem;
}

.MainBTN {
  background: #f24283;
  padding: 0.6rem 1rem;
  margin: 1rem 0;
  border-radius: 2rem;
  color: #fff;
  font-size: 1.2rem;
  border: none;
}
.MainBTN:hover {
  background: #666;
}
.MainBTN a {
  text-decoration: none;
  color: #fff;
}

.Pagination {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 1rem;
}
.Pagination button {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
  background: #999;
  color: #999;
  border: none;
  margin: 0.7rem;
  padding: 0.5rem;
}
.Pagination button.active {
  font-weight: 900;
  background: #f24283;
  color: #f24283;
}

@media (max-width: 661px) {
  .RepoCard {
    width: 16rem;
  }
  .RepoCard .image-content {
    height: 10rem;
  }
  .RepoCard .text-content {
    padding: 0.1rem;
  }
  .RepoCard .avatar {
    width: 8rem;
  }
  .RepoCard p {
    padding: 0.25rem;
  }
  .MainBTN {
    background: #f24283;
    padding: 0.6rem 1rem;
    margin: 1rem 0;
    border-radius: 2rem;
    color: #fff;
    font-size: 1.2rem;
    border: none;
  }
  .MainBTN:hover {
    background: #666;
  }
  .MainBTN a {
    text-decoration: none;
    color: #fff;
  }
  .Pagination {
    margin-top: 0.5rem;
  }
  .Pagination button {
    width: 1rem;
    height: 1rem;
    margin: 0.3rem;
    padding: 0.15rem;
  }
}/*# sourceMappingURL=style.css.map */